<template>
	<div>
		<nav-header></nav-header>
		<nav-bread>
		    <span>Goods</span>
		</nav-bread>
		<div class="accessory-result-page accessory-page">
	        <div class="container">
	          <div class="filter-nav">
	            <span class="sortby">Sort by:</span>
	            <a href="javascript:void(0)" class="default cur">Default</a>
	            <a href="javascript:void(0)" @click="sortGoods" class="price">Price <svg :class="{'sort-up': !sortFlag}" class="icon icon-arrow-short"><use xlink:href="#icon-arrow-short"></use></svg></a>
	            <a href="javascript:void(0)" @click="showFilterTop" class="filterby stopPop">Filter by</a>
	          </div>
	          <div class="accessory-result">
	            <!-- filter -->
	            <div class="filter stopPop" id="filter" :class="{'filterby-show': filterBy}">
	              <dl class="filter-price">
	                <dt>Price:</dt>
	                <dd><a href="javascript:void(0)" @click="priceChecked='all'"  :class="{'cur': priceChecked==='all'}">All</a></dd>
	                <dd v-for="(price, index) in priceFilter" >
	                  <a @click="setPriceFilter(index)" :class="{'cur': index===priceChecked}" href="javascript:void(0)">{{price.startPrice}}-{{price.endPrice}}</a>
	                </dd>
	              </dl>
	            </div>

	            <!-- search result accessories list -->
	            <div class="accessory-list-wrap">
	              <div class="accessory-list col-4">
	                <ul>
	                  <li v-for="item in goodsList">
	                    <div class="pic">
	                      <a href="#">
	                      	<img v-lazy="'/static/'+item.productImage" />
	                      </a>
	                    </div>
	                    <div class="main">
	                      <div class="name">{{item.productName}}</div>
	                      <div class="price">{{item.salePrice}}</div>
	                      <div class="btn-area">
	                        <a href="javascript:;" class="btn btn-cart" @click="addCart(item)">加入购物车</a>
	                      </div>
	                    </div>
	                  </li>
	                </ul>
	                <div v-show="!busy" class="load-more" v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="40">
					  加载更多...
					</div>
	              </div>
	            </div>
	          </div>
	        </div>
        </div>
        <div class="md-overlay" v-show="overLayFlag" @click="closePop"></div>
        <model :mdShow="mdShow" @close="closeModal">
          <p slot="message">
             请先登录,否则无法加入到购物车中!
          </p>
          <div slot="btnGroup">
              <a class="btn btn--m" href="javascript:;" @click="mdShow = false">关闭</a>
          </div>
        </model>
	      <model  :mdShow="mdShowCart" v-on:close="closeModal">
	        <p slot="message">
	          <svg class="icon-status-ok">
	            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-status-ok"></use>
	          </svg>
	          <span>加入购物车成!</span>
	        </p>
	        <div slot="btnGroup">
	          <a class="btn btn--m" href="javascript:;" @click="mdShowCart = false">继续购物</a>
	          <router-link class="btn btn--m btn--red" href="javascript:;" to="/cart">查看购物车</router-link>
	        </div>
	      </model>
		<nav-footer></nav-footer>
	</div>
</template>

<script>
    import NavHeader from '@/components/NavHeader'
    import NavFooter from '@/components/NavFooter'
    import NavBread from '@/components/NavBread'
    import Model from '@/components/Model'
    import {mapMutations} from 'vuex'
	export default {
		data() {
			return {
				goodsList: [],
				priceFilter: [
                   {
                   	  startPrice: '0.00',
                   	  endPrice: '100.00'
                   },
                   {
                   	  startPrice: '100.00',
                   	  endPrice: '500.00'
                   },
                   {
                   	  startPrice: '500.00',
                   	  endPrice: '1000.00'
                   },
                    {
                   	  startPrice: '1000.00',
                   	  endPrice: '5000.00'
                   }
				],
				priceChecked: 'all',
				filterBy: false,
				overLayFlag: false,
				page: 1,
				sortFlag: true,
				pageSize: 8,
				busy: true,
				priceChecked:'all',
				mdShow: false,
				mdShowCart:false

			}
		},
        mounted() {
            this.getGoodsList(false)
            this.getCartCount(true)
        },    
        methods: {
            getGoodsList(flag) {
            	let params = {
            		page: this.page,
            		sort: this.sortFlag ? 1 : -1,
            		pageSize: this.pageSize,
            		priceLevel: this.priceChecked
            	}

	           this.$http.get('/goods/list',{
	           	  params
	           }).then((res) => {
	           	  res = res.data
	           	  if(res.status === '0') {
	           	      if(flag) {
	           	      	this.goodsList = this.goodsList.concat(res.result.list)
	           	      	if(res.result.count == 0) {
	           	      		this.busy = true
	           	      	} else {
	           	      		this.busy = false
	           	      	}
	           	      } else {
	           	      	this.busy = false
	           	      	this.goodsList = res.result.list
	           	      }
                     
	           	  } else {
	           	  	 this.busy = false
	           	  	 this.goodsList = []
	           	  }
	           })
            },
            sortGoods() {
              this.page = 1
              this.sortFlag = !this.sortFlag
              this.getGoodsList()
            },
            showFilterTop() {
               this.filterBy = true
               this.overLayFlag = true
            },
            closePop() {
               this.filterBy = false
               this.overLayFlag = false
            },
            setPriceFilter(index) {
            	this.priceChecked = index
            	this.closePop()
            	this.page = 1
            	this.getGoodsList()
            },
            loadMore: function() {
		      this.busy = true;
		      setTimeout(() => {
		        this.page++
		        this.getGoodsList(true)
		        this.busy = false
		      }, 500);
		    },
		    addCart(item) {
		    	let productId = item.productId
		    	let params = {
		    		productId
		    	}
               this.$http.post('/goods/addCart', {
               	 	productId
               }).then((res) => {
               	  res = res.data
               	  if(res.status == 0) {
               	  	 this.updateUserCartCount(1)
               	  	 this.getCartCount()
               	  	 this.mdShowCart = true
               	  } else {
               	  	this.mdShow = true
               	  }
               })
               
		    },
		    getCartCount(flag) {
				this.$http.get('/users/getCartCount').then((res) => {
                    res = res.data
                    if(res.status == 0) {
                    	if(flag) {
                    	   this.updateUserCartCount(res.result)
                    	} else {
                    		console.log('update cartConut')
                    	}
                    }
				})
		    },
		    closeModal() {
                this.mdShow = false
                this.mdShowCart = false
		    },
		    ...mapMutations([
              'updateCartCount',
              'updateUserCartCount'
		    ])
        },
		components: {
			NavHeader,
			NavFooter,
			NavBread,
			Model
		}
	}
</script>


